import fetch from './fetch.js'

class User {
    static UserRegister (data) {
        return fetch('UserInfo', data)
    }
    static UserLogoin (data) {
        return fetch('UserInfo', data)
    }
    // 
    static UserInfoManage (data) {
        return fetch('UserInfo', data)
    }
    // 
    static UpdatePasswd (data) {
        return fetch('UserInfo', data)
    }
}

export default User