<template>
    <div class="app-view-wrapper">
        <header>
            <the-title :title="msg"></the-title>
        </header>
        <div class="container">
            <div class="txt">
                <p></p>
                <p> {{tel}} </p>
            </div>
            <div class="code">
                <span></span>
                <input type="number" placeholder="" autocomplete="off" v-model.trim="code">
                <a class="sms_code" @click="getCode">{{codeMsg}}</a>
            </div>
            <div class="tip">
                <p class="tip-title"></p>
                <p></p>
            </div>
            <div class="btn">
                <button @click="toNewTel"></button>
            </div>
        </div>
    </div>
</template>
<script>
import TheTitle from 'components/TheTitle.vue'

export default {
    components: {
        TheTitle
    },
    data(){
        return{
            msg: '',
            tel: '',
            codeMsg: '',
            countdown: 60,
            timer: null,
            code: ''
        }
    },
    created(){
        this.tel = 13522877978
    },
    methods: {
        toBack(){
            this.$router.back(-1)
        },
        toNewTel(){
            this.$router.push('../userInfo/newtel')
        },
        getCode(){
            if(this.countdown !== 60) return
            this.timer = setInterval(() => {
                this.codeMsg = `${this.countdown}`
                this.countdown --
                if(this.countdown === 0){
                    clearInterval(this.timer)
                    this.timer = null
                    this.countdown = 60
                    this.codeMsg = ''
                }
            }, 1000)
        },
    }   
}
</script>
<style scoped>
    .app-view-wrapper{
        width: 100%;
        height: 100vh;
        background: #F3F3F4;
        overflow: hidden;
    }
    .container{
        padding-top: 50px;
    }
    .container .txt{
        margin: 40px 0;
        font-size: 15px;
        color: #000;
    }
    .container .txt p{
        line-height: 28px;
    }
    .container .code{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 14px;
        background: #fff;
        border-bottom: 1px solid #ddd;
        text-align: left;
        color: #000;
    }
    .container .code input{
        width: 150px;
    }
    .sms_code{
        padding: 8px 16px;
        background: #FF3947;
        border-radius: 30px;
        font-size: 14px;
        color: #fff;
    }
    .container .tip{
        margin: 16px 14px;
        text-align: left;
        font-size: 12px;
        color: #939393;
    }
    .container .tip .tip-title{
        line-height: 26px;
        color: #FF3947;
    }
    .btn{
        padding: 80px 14px 0 14px;
    }
    .btn button{
        width: 100%;
        padding: 14px 0;
        border: none;
        background: #FF3947;
        border-radius: 60px;
        font-size: 20px;
        color: #fff;
        letter-spacing: 4px;
    }
</style>
