import Vue from 'vue'
import Vuex from 'vuex'
import fetch from 'api/fetch.js'

Vue.use(Vuex)

export default new Vuex.Store({
    state: {
        userInfo: null,
        // isLogin: false
    },
    getters: {
        isLogin: state => {
            return !!state.userInfo
            // return false
            // state.isLogin
        }
    },
    // 
    mutations: {
        // 
        changeLoginStatu(state, info){
            state.userInfo = info
        }
    },
    //  mutations
    actions: {
        // 
        getUserInfo({commit}){
            fetch('UserLogoin').then(res => {
                commit('changeLoginStatu', res)
            })
        }
    }
})