import fetch from './fetch.js'

class Address {
    // 
    static GetUserAddress (data) {
        return fetch('UserInfo', data)
    }
    // 
    static UserAddressManage (data) {
        return fetch('UserInfo', data)
    }
    // 
    static SetDefaultAddress (data) {
        return fetch('UserInfo', data)
    }
}

export default Address